import { Container, Typography } from "@mui/material";

function Etusivu() {
    return(
        <Container style={{textAlign:"center",padding:"50px"}}>
        <Typography variant="h4">Tervetuloa vitsigeneraattoriin!</Typography>
        <br/>
        <Typography variant="h5">Lis vitsi -nkymss voit kirjata palveluun oman vitsisi.</Typography>
        <br/>
        <Typography variant="h5">Vitsikortit -nkymss pset lukemaan vitsej satunnaisessa jrjestyksess.</Typography>
        <br/>
        <Typography variant="h5">Ja viimeisen Lista -nkymss on listattuna jokainen kirjattu vitsi, joita pset mys muokkaamaan tai halutessasi poistamaan.</Typography>
        </Container>
    );
}
export default Etusivu;